:root {
  --white-color: #fff;
  --blue-color: #00114f;
  --main-font: "Compose", sans-serif;
  --roboto: "Roboto", sans-serif;
  --transition: all 0.2s ease-out;
}

*,
p,
h1,
h2,
h3 {
  padding: 0;
  margin: 0;
}

body {
  font-family: var(--main-font);
  color: var(--blue-color);
  background-color: #fff;
  min-width: 320px;
}
body.lock {
  overflow: hidden;
}

* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

body {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  min-height: 100vh;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.container {
  width: 100%;
  max-width: 1500px;
  min-width: 320px;
  margin: 0 auto;
  padding: 0 63px;
}
@media only screen and (max-width: 1399px) {
  .container {
    padding: 0 40px;
  }
}
@media (max-width: 768px) {
  .container {
    padding: 0 28px;
  }
}

.header.white,
.header.white .logo__main,
.header.white .menu__link,
.header.white .auth__login,
.header.white .auth__signup,
.header.white .lang__selected .lang__value {
  color: var(--white-color);
}
.header.white .auth__signup {
  border: 1px solid rgba(255, 255, 255, 0.6);
}
.header.white .auth__signup:hover {
  border: 1px solid var(--blue-color);
}
.header.white .menu__item::after {
  border: 1px solid var(--white-color);
}
.header.white .header__line,
.header.white .auth::after {
  background-color: var(--white-color);
}
.header.white .header__burger span, .header.white .header__burger::after, .header.white .header__burger::before {
  background-color: var(--white-color);
}

.header.dark,
.header.dark .logo__main,
.header.dark .menu__link,
.header.dark .auth__login,
.header.dark .auth__signup,
.header.dark .lang__selected .lang__value {
  color: var(--blue-color);
}
.header.dark .auth__signup {
  border: 1px solid rgba(0, 17, 79, 0.6);
}
.header.dark .auth__signup:hover {
  border: 1px solid var(--blue-color);
}
.header.dark .menu__item::after {
  border: 1px solid var(--blue-color);
}
.header.dark .header__line,
.header.dark .auth::after {
  background-color: var(--blue-color);
}
.header.dark .header__burger span, .header.dark .header__burger::after, .header.dark .header__burger::before {
  background-color: var(--blue-color);
}

.header {
  position: absolute;
  top: 41px;
  left: 0;
  width: 100%;
  z-index: 1000;
}

.header__container {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
}
@media (max-width: 1280px) {
  .header__container {
    padding: 0 51px 0 30px;
  }
}
@media only screen and (max-width: 350px) {
  .header__container {
    padding: 0 20px;
  }
}

.header__navbar {
  display: flex;
  align-items: center;
}

.header__logo {
  margin-right: 38px;
}
@media (max-width: 1280px) {
  .header__logo {
    position: relative;
    z-index: 20;
    margin-right: 0;
  }
}

.logo {
  display: flex;
  align-items: center;
}
@media (max-width: 1280px) {
  .logo {
    display: block;
  }
  .logo.active .logo__main {
    color: var(--blue-color) !important;
  }
  .logo.active .logo__main::after {
    opacity: 1;
    visibility: visible;
  }
  .logo.active .logo__descr {
    opacity: 0;
    visibility: hidden;
  }
}

.logo__main {
  position: relative;
  top: -5px;
  margin-right: 18px;
  font-weight: 700;
  font-size: 35px;
  line-height: 44px;
  text-decoration: none;
  transition: var(--transition);
}
.logo__main::after {
  content: "Investment Management";
  position: absolute;
  left: 0;
  bottom: -5px;
  font-weight: 400;
  font-size: 10px;
  line-height: 100%;
  letter-spacing: -0.01em;
  color: var(--blue-color);
  opacity: 0;
  visibility: hidden;
  transition: all 0.3s ease-out;
}

.logo__descr {
  font-weight: 500;
  font-size: 10px;
  line-height: 100%;
  letter-spacing: -0.01em;
  transition: var(--transition);
}

.header__line {
  margin-right: 51px;
  width: 1px;
  height: 59px;
}
@media only screen and (max-width: 1399px) {
  .header__line {
    margin-right: 35px;
  }
}
@media (max-width: 1280px) {
  .header__line {
    display: none;
  }
}

.header__nav {
  display: flex;
  align-items: center;
  transition: var(--transition);
  transition: all 0.3s ease-out;
}
@media (max-width: 1280px) {
  .header__nav {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 10;
    background-color: #fff;
    opacity: 0;
    visibility: hidden;
    pointer-events: none;
  }
  .header__nav.active {
    opacity: 1;
    visibility: visible;
    pointer-events: all;
  }
}

.header__menu {
  margin-right: 117px;
}
@media only screen and (max-width: 1399px) {
  .header__menu {
    margin-right: 60px;
  }
}

.menu__list {
  min-width: 394px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media only screen and (max-width: 1399px) {
  .menu__list {
    min-width: 380px;
  }
}

.menu__item {
  position: relative;
  list-style-type: none;
}
.menu__item::after {
  content: "";
  position: absolute;
  bottom: -3px;
  left: 0;
  width: 100%;
  height: 1px;
  background-color: var(--white-color);
  visibility: hidden;
  opacity: 0;
  transition: var(--transition);
}
.menu__item:hover::after {
  visibility: visible;
  opacity: 1;
  transform: translateY(4px);
}

.menu__link {
  font-weight: 400;
  font-size: 21px;
  line-height: 26px;
  letter-spacing: -0.01em;
  text-decoration: none;
}

.lang {
  position: relative;
}
.lang .lang__shevron {
  transition: var(--transition);
}
.lang.active .lang__shevron {
  transform: scaleY(-1);
}
.lang.active .lang__selected::after {
  display: block;
}

.lang__selected {
  display: flex;
  align-items: center;
  gap: 0 12px;
  cursor: pointer;
}
.lang__selected .lang__icon img {
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}
.lang__selected::after {
  display: none;
  content: "";
  position: absolute;
  top: 28px;
  left: -30%;
  right: -50%;
  height: 70px;
}

.lang__value {
  font-weight: 700;
  font-size: 19px;
  line-height: 24px;
  letter-spacing: -0.01em;
}

.lang__icon {
  width: 29px;
  height: 29px;
}
.lang__icon img {
  display: block;
  width: 100%;
  height: auto;
  border-radius: 50%;
}

.lang.active .lang__dropdown {
  visibility: visible;
  opacity: 1;
}

.lang__dropdown {
  position: absolute;
  top: 67px;
  left: -32px;
  z-index: 2px;
  width: 205px;
  min-height: 228px;
  padding: 13px 13px 20px;
  background: var(--white-color);
  box-shadow: 0px 4px 34px rgba(0, 0, 0, 0.1);
  border-radius: 10px;
  visibility: hidden;
  opacity: 0;
  transition: var(--transition);
}
.lang__dropdown .lang__value {
  color: var(--blue-color);
}

.lang__item {
  display: flex;
  align-items: center;
  gap: 0 13px;
  padding: 7px 14px 5px;
  cursor: pointer;
  transition: var(--transition);
}
.lang__item .lang__icon {
  width: 35px;
  height: 35px;
}
.lang__item .lang__value {
  font-weight: 600;
  font-size: 17px;
  line-height: 37px;
  color: var(--blue-color);
}
.lang__item:hover {
  background-color: rgba(0, 17, 79, 0.1);
  border-radius: 6px;
}

.header__panel {
  display: flex;
  align-items: center;
}
@media (max-width: 1280px) {
  .header__panel {
    position: relative;
    z-index: 20;
    top: -15px;
    justify-content: space-between;
    min-width: 115px;
  }
}

.auth {
  display: flex;
  align-items: flex-end;
}
@media (max-width: 1280px) {
  .auth {
    position: relative;
  }
  .auth::after {
    content: "";
    position: absolute;
    top: 50%;
    right: 0;
    transform: translateY(-50%);
    transition: var(--transition);
    width: 1px;
    height: 59px;
  }
  .auth.active::after {
    background-color: var(--blue-color) !important;
  }
  .auth.active path {
    stroke: var(--blue-color);
  }
}

.auth__login,
.auth__signup {
  display: flex;
  align-items: flex-end;
  height: 68px;
  margin-top: 9px;
  font-weight: 700;
  font-size: 19px;
  line-height: 24px;
  letter-spacing: -0.01em;
  text-decoration: none;
  transition: var(--transition);
}

.auth__login {
  margin-right: 20px;
  width: 145px;
  padding: 24px 0 18px 0;
  justify-content: center;
  background-color: transparent;
  border: none;
  cursor: pointer;
}
.auth__login:hover {
  background-color: var(--blue-color);
  border-radius: 6px;
}
.auth__login svg {
  display: none;
}
@media (max-width: 1280px) {
  .auth__login {
    width: auto;
    height: auto;
    margin-right: 34px;
    margin-top: 0;
  }
  .auth__login span {
    display: none;
  }
  .auth__login svg {
    display: block;
  }
  .auth__login:hover {
    background-color: transparent;
  }
  .auth__login path {
    transition: var(--transition);
  }
}

.auth__signup {
  width: 188px;
  padding: 24px 30px 18px 41px;
}
@media (max-width: 1280px) {
  .auth__signup {
    display: none;
  }
}
.auth__signup .icon__arrow {
  display: inline-block;
  margin-left: 27px;
  transform: rotate(-45deg);
  transition: var(--transition);
}
.auth__signup:hover {
  background-color: var(--blue-color);
  border: 1px solid var(--blue-color);
  border-radius: 6px;
}
.auth__signup:hover .icon__arrow {
  transform: rotate(0);
}

.dark .auth__login:hover,
.dark .auth__signup:hover {
  color: var(--white-color);
}

.dark .auth__signup:hover path {
  fill: var(--white-color);
  stroke: var(--white-color);
}

.header__burger {
  display: none;
  position: relative;
  width: 31px;
  height: 23px;
  align-items: center;
  cursor: pointer;
}
@media (max-width: 1280px) {
  .header__burger {
    display: flex;
  }
}
.header__burger span, .header__burger::before, .header__burger::after {
  min-width: 100%;
  height: 3px;
  border-radius: 20px;
  background-color: var(--white-color);
}
.header__burger span {
  display: block;
  transition: var(--transition);
}
.header__burger::before {
  content: "";
